/* ========================== Global Styles =================== */
body {
    margin: 0;
    font-family: Helvetica, sans-serif;
  }
  
  body, html, #root {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .serif {
    font-family: Georgia, serif;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
  
  
  .align-center {
    width: 100%;
    text-align: center;
  }
  
  button, div {
    -webkit-tap-highlight-color: transparent;
  }
  
  button:focus, input:focus {
    outline: none;
  }




/* ========================== Banner ========================== */ 


section.banner {
    box-shadow: rgba(0, 0, 0, 0.6) 0px -10px 200px 5px;
    height: 50%;
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #fff;
    transition: height cubic-bezier(0.165, 0.84, 0.44, 1) 0.2s;
}

section.banner.completed {
    height: 0%;
}

section.banner.paused {
    height: 0%;
}

section.banner.expanded {
    height: 85%;
}

section.banner .banner-wrapper {
    width: 60%;
    margin: 0 auto;
    height: 100%;
    text-align: center;
    padding: 30px 0 0 0;
}

section.banner h1 {
    margin: 15px 0 5px 0;
    font-size: 1.2rem;
    font-weight: 600;
    color: #383838;
}

section.banner p {
    font-size: 1rem;
    line-height: 1.7rem;
    margin: 10px auto 20px auto;
    width: 50%;
    color: #383838;
}

section.banner button {
    outline: none;
    border: none;
    height: 2.5rem;
    line-height: 2.5rem;
    padding: 0 30px;
    font-size: 0.9rem;
    background-color: #383838;
    color: #fff;
    border-radius: 3px;
    cursor: pointer;
}

section.banner button:hover {
    background-color: #1b1b1b;
    color: #fff;
    transition: background ease-in-out 0.3s;
}

section.banner span.or-text {
    text-transform: uppercase;
    color: #505050;
    font-size: 0.8rem;
    margin: 15px 0;
    display: block;
}

section.banner a {
    text-decoration: none;
    color: #383838;
    border-bottom: 0.85px solid #383838;
    transition: border ease-out 0.2s;
}

section.banner a:hover {
    text-decoration: none;
    color: #505050;
    border-bottom: 0.85px solid #38383800;
}


section.banner .options-container {
    display: flex;
    width: 100%;
}

section.banner .badge-container {
    width: 300px;
    height: 200px;
    background-color: #fff;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
    transition: border ease-in-out 0.2s;
    overflow: hidden;
    border: 1px solid #aaaaaa;
}


section.banner .badge-container:not(:last-of-type) {
    margin-right: 20px;
}

section.banner .badge-container:hover {
    border: 1px solid #444444;
}

section.banner .option-badge {
    width: 100%;
    height: 100%;
    padding: 10px;
}

section.banner .option-badge h1 {
    margin: 7.5px 0;
}

section.banner .option-badge .icon {
    background-color: #22254f;
    color: #fff;
    height: 45px;
    width: 45px;
    line-height: 45px;
    font-size: 22px;
    text-align: center;
    border-radius: 50%;
    margin: 0 auto;
}

section.banner .option-badge button {
    background-color: #23ac57;
    color: #fff;
    text-transform: uppercase;
    font-size: 0.85rem;
    outline: none;
    border: none;
    box-shadow: none;
    border-radius: 2px;
    letter-spacing: 0.3px;
    width: 85%;
    margin: 15px auto 0px auto;
    padding: 10px 0;
    cursor: pointer;
    transition: background ease-in-out 0.2s;
}

section.banner .option-badge:hover button {
    background: #16803f;
}

section.banner .option-badge p {
    margin: 5px;
    font-size: .8rem;
    color: #4a4a4a;
}


/* ========================== Widget ========================== */ 

.widget-container {
    width: 100%;
    padding: 20px 0 0 0;
    height: 100%;
}

.widget-container .left-section {
    display: inline-block;
    width: 35%;
    height: 100%;
    vertical-align: top;
    text-align: center;
    padding: 10px;
}

.widget-container .left-section p {
    text-align: left;
}

.widget-container .left-section .retry-callout {
    width: 100%;
    background-color: #ca3030;
    color: #fff;
    padding: 10px;
    margin: 5px auto;
    font-size: 0.85rem;
    border-radius: 2px;
}

.widget-container .right-section {
    display: inline-block;
    width: 65%;
    height: 80%;
    vertical-align: top;
}

.widget-title {
    border-bottom: 1px solid #c5c5c5;
    transition: border ease 0.2s;
}

.widget-title.red {
    border-bottom: 1px solid #ca3030;
}

.widget-title h1 {
    font-family: 'Halant', serif;
    margin: 5px 0;
    font-size: 1.2rem;
}

.image-grid {
    display: flex;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    align-content: flex-start;
    margin: 1em 0;
    background-position: center;
    background-size: cover;
}

.image-grid .grid-box {
    width: calc(100% / 3);
    height: calc(100% / 3);
    cursor: pointer;
    transition: background ease-in-out 0.2s;
    border: 2px solid #fff;
}

.image-grid .grid-box .grid-display {
    height: 100%;
    width: 100%;
}

.image-grid .grid-box.pristine .grid-display {
    background: none;
}

.image-grid .grid-box.selected .grid-display {
    background: rgba(73, 50, 204, 0.418);
}

.widget-container button {
    background-color: #23ac57;
    color: #fff;
    text-transform: uppercase;
    font-size: 0.85rem;
    outline: none;
    border: none;
    box-shadow: none;
    border-radius: 2px;
    letter-spacing: 0.3px;
    width: 60%;
    margin: 20px auto;
    padding: 20px 0;
    cursor: pointer;
    transition: background ease-in-out 0.2s;
    display: block;
}

.widget-container button:hover {
    background: #16803f;
}

.button-container {
    padding: 0.75em;
    text-align: center;
    width: 100%;
}

