@import url('https://fonts.googleapis.com/css?family=Hind&display=swap');

.kl-paid-banner, .kl-paid-banner * {
    font-family: 'Hind', sans-serif;
}


.kl-paid-banner {
    width: 100%;
    height: 450px;
    box-shadow:  #0c0c0c36 0px -20px 80px 80px;
    position: absolute;
    bottom: 0;
    padding: 15px 0;
    -webkit-animation-name: slide-up; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 0.5s; /* Safari 4.0 - 8.0 */
    animation-name: slide-up;
    animation-duration: 0.5s;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes slide-up {
    from {bottom: -450px}
    to {bottom: 0px}
}
  
  /* Standard syntax */
@keyframes slide-up {
    from {bottom: -450px}
    to {bottom: 0px}
}


.kl-banner-content-wrapper {
    width: 55%;
    height: 100%;
    margin: 0 auto;
}

.kl-paid-banner a {
    color: #dd3333;
    text-decoration: none;
}

.kl-paid-banner a:hover {
    text-decoration: underline;
}

.kl-paid-banner .row.sign-in-row {
    text-align: center;
}

.kl-paid-banner .row.sign-in-row hr {
    height: 3px;
    border: none;
    background:-webkit-linear-gradient(180deg,#000 50%,#dd3333 50%);
    width: 30px;
    margin: 15px auto;
}

/* <===================== Subscription Option Card ========================> */

.kl-option-card {
    width: 100%;
    padding: 20px;
    background-color: #000;
    color: #fff;
    border-radius: 3px;
}

.kl-option-card h1 {
    font-weight: 400;
    margin: 0;
    font-size: 1.7rem;
}

.kl-option-card h2 {
    font-weight: 400;
    margin: 0;
    font-size: 1.4rem;
}

.kl-option-card .card-row {
    width: 100%;
    display: block;
}

.kl-option-card .card-row .card-col {
    display: inline-block;
}

.kl-option-card button {
    background: #f5f5f5;
    padding: 5px 30px; 
    outline: none;
    font-size: 1rem;
}